<template>
  <v-card class="pt-7 pb-3">
    <div class="card-body">
      <v-form @submit.prevent="createProject" ref="form" v-model="form">
        <label for="title" class="h3 mb-5">Nouveau Projet</label>
        <v-text-field
          label="Titre"
          outlined
          :rules="[v => !!v || 'Titre obligatoire']"
          v-model="newProject.title"
          :disabled="loading"
        ></v-text-field>
        <v-select
          label="Type"
          type="type"
          id="type"
          :items="types"
          :rules="[v => !!v || 'Type obligatoire']"
          solo
          max-width="200px"
          v-model="newProject.type"
        ></v-select>
        <v-textarea
          label="Description"
          type="description"
          id="description"
          outlined
          :rules="[v => !!v || 'Description obligatoire']"
          name="input-7-4"
          v-model="newProject.description"
          :disabled="loading"
        ></v-textarea>
        
        <v-chip-group>
          <v-chip v-for="event in newProject.events" :key="event.start">
            {{event.chip}}
          </v-chip>
        </v-chip-group>

        <v-card-actions class="d-flex justify-center">
          <v-btn @click="pickingDate=true">Ajouter un événement dans le calendrier</v-btn>
        </v-card-actions>

        <v-card-actions class="d-flex justify-center">
          <v-btn @click="cancel" :disabled="loading">
            Annuler
          </v-btn>
          <v-btn type="submit" :disabled="loading || !form">
            Valider
          </v-btn>
        </v-card-actions>

        <v-progress-linear
          v-if="loading"
          color="green darken-4 accent-4"
          indeterminate
          rounded
          height="6"
          class="progress"
        ></v-progress-linear>
      </v-form>
    </div>

    <v-expand-transition>
      <v-card
        v-if="pickingDate"
        class="transition-fast-in-fast-out v-card--reveal"
        style="height: 100%;"
      >
        <ChooseDate 
        @closeDatePicker="closeDatePicker"
        @addEvent="addEvent"/>
      </v-card>
    </v-expand-transition>
    
  </v-card>
</template>

<script>
import { mapActions } from "vuex";
import axios from "axios";
import ChooseDate from '@/components/project/ChooseDate.vue';

export default {
  name: "CreateProject",
  data() {
    return {
      form: false,
      loading: false,
      pickingDate: false,
      newProject: {
        events: []
      },
      types: ["commun", "idée", "perso"]
    };
  },
  components:{
    ChooseDate
  },
  props: {
    project: Object
  },
  methods: {
    ...mapActions("project", ["sendCreateProject"]),
    cancel() {
      this.newProject = {events: []};
      this.$refs.form.reset();
      this.$emit("closeCreation");
    },
    createProject() {
      this.loading = true;
      this.sendCreateProject(this.newProject)
        .then(() => {
          this.loading = false;
          this.$emit("closeCreation");
          this.newProject = {events: []};
          this.$refs.form.reset();
        })
        .catch(() => {
          this.loading = false;
        });
    },
    closeDatePicker(){
      this.pickingDate = false
    },
    addEvent(event){
      this.newProject.events.push(event)
    }
  }
};
</script>

<style>
  
.v-card--reveal {
  bottom: 0;
  opacity: 1 !important;
  position: absolute;
  width: 100%;
}

</style>
